.new-expense__controls {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-bottom: 1rem;
    text-align: left;
  }
  
  .new-expense__control label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
  }
  
  .new-expense__control input {
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }

  .new-expense__control div {
    font-weight: bold;
    width: 20rem;
  }
  .new-expense__control div h2{
    margin: 0;
    margin-top: 2rem;
  }
  
  .new-expense__actions {
    text-align: right;
  }


  .incorrect {
    background-color: rgb(255, 123, 123);
  }

  